.timeline {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px 0;
  position: relative;
}

.timeline:after {
  content: "";
  background-color: var(--primary-color);
  width: 5px;
  margin-left: -3px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
}

.timeline-item {
  background-color: inherit;
  width: 50%;
  position: relative;
}

.timeline-item:after {
  content: "";
  background-color: var(--background);
  z-index: 1;
  border: 4px solid #d7ff37;
  border-radius: 50%;
  width: 23px;
  height: 23px;
  position: absolute;
  top: 90px;
  right: -13px;
}

.timeline-item:nth-child(odd) {
  right: 10%;
}

.timeline-item:nth-child(2n) {
  left: 60%;
}

.timeline-item:nth-child(2n):after {
  left: -115px;
}

.timeline-item:nth-child(odd):after {
  right: -115px;
}

.timeline-content {
  letter-spacing: 1px;
  background-color: #0e0e0e;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 20px 40px;
  position: relative;
}

.timeline-content:hover {
  box-shadow: 1px 1px 10px #ffffff76;
}

.timeline-content h2 {
  color: #bbb;
}

.timeline-content h3 {
  color: var(--primary-color);
}

.timeline-content .highlight {
  color: #99b659;
  font-size: 23px;
}

@media (width <= 768px) {
  .timeline {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .timeline-item {
    text-align: left;
    width: 80%;
    margin-bottom: 30px;
  }

  .timeline-item:after, .timeline:after {
    display: none;
  }

  .timeline-item:nth-child(odd) {
    right: 0;
  }

  .timeline-item:nth-child(2n) {
    left: 0;
  }

  .timeline-content {
    padding: 20px;
  }
}

